<template>
  <div class="topNav">
    <button type="button" class="btn btn-sm btn-success" @click="addDepartmentClick"></button>
    <table class="table table-striped table-bordered table-hover text-center top15">
      <thead>
      <tr>
        <th class="text-center"></th>
        <th class="text-center"></th>
        <th class="text-center"></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(item, index) in departments" :key="index" @click="delegateAppItemClick($event,item,index)">
        <td>{{item.departmentName}}</td>
        <td>{{item.departmentDesc}}</td>
        <td class="text-center">
          <button type="button" class="btn btn-xs btn-danger"></button>
          <button type="button" class="btn btn-xs btn-default"></button>
          <button type="button" class="btn btn-xs btn-info" v-show="false"></button>
          <button type="button" class="btn btn-xs btn-primary" v-show="false"></button>
        </td>
      </tr>
      </tbody>
    </table>

    <!--
---
-->
    <ModalDialog title="" :close-click="addDepartmentModalCloseClick"
                 :sure-click="addDepartmentModalSureClick" v-show="departmentAddModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center"></label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role name (1~16)"
                     v-model="departmentInfo.departmentName">
            </div>
            <span class="text-info small"></span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center"></label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="role description (1~128)"
                        v-model="departmentInfo.departmentDesc"></textarea>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
---
-->
    <ModalDialog title="" :close-click="updateDepartmentModalCloseClick"
                 :sure-click="updateDepartmentModalSureClick" v-show="departmentUpdateModalDialogShow">
      <div slot="modalBody">
        <div class="form-group top15">
          <div class="row">
            <label class="col-sm-2 text-center"></label>
            <div class="col-sm-9">
              <input type="text" class="form-control" placeholder="role name (1~16)"
                     v-model="currentDepartmentInfo.departmentName">
            </div>
            <span class="text-info small"></span>
          </div>
        </div>
        <div class="form-group">
          <div class="row">
            <label class="col-sm-2 text-center"></label>
            <div class="col-sm-9">
              <textarea rows="3" type="text" class="form-control" placeholder="role description (1~128)"
                        v-model="currentDepartmentInfo.departmentDesc"></textarea>
            </div>
          </div>
        </div>
      </div>
    </ModalDialog>

    <!--
---
-->
    <ModalDialog title="" :close-click="deleteDepartmentModalCloseClick"
                 :sure-click="deleteDepartmentModalSureClick" v-show="departmentDeleteModalDialogShow">
      <div slot="modalBody" style="padding-left: 15px">
        <p class="top15"><span class="text-danger"></span>:</p>
      </div>
    </ModalDialog>

    <!--
---
-->
    <ModalDialog title="" :close-click="authorizationDepartmentModalCloseClick"
                 :sure-click="authorizationDepartmentModalSureClick" v-show="departmentAuthorizationModalDialogShow">
      <div slot="modalBody" style="padding-left: 15px">
        <div class="form-group top15">
          <p class="text-info text-center"></p>
          <label class="checkbox-inline" v-for="(item, index) in tenantRoleInfos" :key="index">
            <input type="checkbox" :value="item.id" :disabled="item.id<3" v-model="checkUserRoleIds">
            {{item.roleName}}
          </label>
        </div>
      </div>
    </ModalDialog>

  </div>
</template>

<script>

  import ModalDialog from './comment/ModelDialog'

  export default {
    name: 'TortoiseDepartment',
    components: {
      ModalDialog
    },
    data () {
      return {
        //
        departments: [],
        departmentOperate: false,
        departmentAddModalDialogShow: false,
        departmentUpdateModalDialogShow: false,
        departmentDeleteModalDialogShow: false,
        departmentAuthorizationModalDialogShow: false,
        departmentInfo: {
          departmentName: '',
          departmentDesc: ''
        },
        //itemindex
        currentDepartmentInfoIndex: 0,
        //id
        currentDepartmentInfo: {},
        //
        tenantRoleInfos: [],
        //
        checkUserRoleIds: []
      }
    },
    beforeDestroy () {
      if (this.departmentOperate === true) {
        //
        this.$global.getAuthInfo().object.departmentInfos = this.departments
        sessionStorage.setItem(this.$global.cacheKey.authInfoKey, JSON.stringify(this.$global.getAuthInfo()))
        this.departmentOperate = false
      }
    },
    mounted () {
      this.departments = this.$global.getAuthInfo().object.departmentInfos != null ? this.$global.getAuthInfo().object.departmentInfos : []
      //
      if (this.tenantRoleInfos.length === 0) {
        if (this.$global.getRoleInfos() == null) {
          this.$axios.get(this.$global.url.role.get)
            .then(response => {
              if (response !== null) {
                this.tenantRoleInfos = response.data.data
                //
                sessionStorage.setItem(this.$global.cacheKey.roleInfosKey, JSON.stringify(response.data.data))
              }
            })
        } else {
          this.tenantRoleInfos = this.$global.getRoleInfos()
        }
      }
    },
    methods: {
      addDepartmentClick () {
        this.departmentInfo = {
          departmentName: '',
          departmentDesc: ''
        }
        this.departmentAddModalDialogShow = true
      },
      addDepartmentModalCloseClick () {
        this.departmentAddModalDialogShow = false
      },
      addDepartmentModalSureClick () {
        this.departmentAddModalDialogShow = false
        if (this.departmentInfo.departmentName.length > 0) {
          let d = new Date()
          let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
          this.$axios.post(this.$global.url.department.add, this.departmentInfo)
            .then(response => {
              if (response !== null) {
                let departmentInfo = response.data.data
                departmentInfo.updateDate = currentDate
                departmentInfo.createDate = currentDate
                this.departments.push(departmentInfo)
                //
                this.departmentOperate = true
                //
                this.$pubSub.publish('toastShow', {
                  type: 'success',
                  msg: ''
                })
              }
            })
        } else {
          this.$pubSub.publish('toastShow', {
            type: 'warning',
            msg: ''
          })
        }
      },
      updateDepartmentModalCloseClick () {
        this.departmentUpdateModalDialogShow = false
      },
      updateDepartmentModalSureClick () {
        this.departmentUpdateModalDialogShow = false
        let d = new Date()
        let currentDate = d.getFullYear() + '-' + d.getMonth() + '-' + d.getDate() + ' ' + d.getHours() + ':' + d.getMinutes() + ':' + d.getSeconds()
        this.$axios.put(this.$global.url.department.update, this.currentDepartmentInfo)
          .then(response => {
            if (response !== null) {
              let departmentInfo = response.data.data
              departmentInfo.updateDate = currentDate
              //
              this.departments.splice(this.currentDepartmentInfoIndex, 1, departmentInfo)
              //
              this.departmentOperate = true
              //
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: ''
              })
            }
          })
      },
      deleteDepartmentModalCloseClick () {
        this.departmentDeleteModalDialogShow = false
      },
      deleteDepartmentModalSureClick () {
        this.departmentDeleteModalDialogShow = false
        this.$axios.delete(this.$global.url.department.delete, {
          params: {
            id: this.currentDepartmentInfo.id
          }
        })
          .then(response => {
            if (response !== null) {
              this.departments.splice(this.currentDepartmentInfoIndex, 1)
              //
              this.departmentOperate = true
              //
              this.$pubSub.publish('toastShow', {
                type: 'success',
                msg: ''
              })
            }
          })
      },
      authorizationDepartmentModalCloseClick () {
        this.departmentAuthorizationModalDialogShow = false
      },
      authorizationDepartmentModalSureClick () {
        this.departmentAuthorizationModalDialogShow = false

      },
      delegateAppItemClick (e, item, index) {
        this.currentDepartmentInfoIndex = index
        this.currentDepartmentInfo = item
        switch (e.target.innerText) {
          case '':
            this.departmentDeleteModalDialogShow = true
            break
          case '':
            this.departmentUpdateModalDialogShow = true
            break
          case '':
            this.checkUserRoleIds = []
            this.departmentAuthorizationModalDialogShow = true
            break
          case '':
            break
          default:
            break
        }
      }
    }
  }
</script>

<style scoped>

</style>



